*{
  margin: 0;
  padding: 0;
  text-decoration: none;
};
.container{
  display: flex;
}
main{
  width: 100%;
  padding: 20px;
}
.sidebar{
  background-color: #FF5331;
  color: white;
  height: 100vh;
  width: 20%;
  transition: all 0.5s;
}
.topSection{
  display: flex;
  align-items: center;
  padding: 20px 15px;
}
.logo{
  width: 75%;
}
.bars{
  display: flex;
  font-size: 25px;
}
.link{
  display: flex;
  color: white;
  padding: 10px 15px;
  gap: 15px;
  transition: all 0.5s;
}
.link:hover{
  background: #ec755d;
  color: black;
  border-radius: 10px;
  transition: all 0.5s;
}
.active{
  background: #ec755d;
  color: black
}
.icon, .linkTest{
  font-size: 20px;
}
.linkTest{
  width: 100%;
}